import * as React from 'react';
import { Image } from 'react-bootstrap';
import { Helmet } from 'react-helmet'
import { getFixedT } from 'i18next';
import logoPng from './../../../assets/images/100g_vert.png'
import 'bootstrap/dist/css/bootstrap.min.css';
import './../css/auth.css'
import backgroundPng from './../../../assets/images/2fa.png'

const SentSuccess = () => {
    const t = getFixedT(null, null, 'forgetPassword')

    return (<>
        <Helmet title={t('title')} />
        <div class="card card1">
            <div class="row justify-content-center my-auto">
                <div class="col-md-8 col-10">
                    <div class="row justify-content-center px-3 py-4 mb-3"> <Image id="logo" src={logoPng} /> </div>
                    <h5 class="m-0 my-2 my-2">{t('Successfully Sent! Please check your inbox')}</h5>
                </div>
            </div>
        </div>
        <div class="card card2" style={{ backgroundImage: `url(${backgroundPng})` }}>
        </div>


    </>

    )
}

export default SentSuccess;